body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dark-mode{
  background-color: black;
}

.dark-mode h1 {
color: white;
}

.container{
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
  min-height: 100vh;
}


.notes-list{
display: grid;
grid-gap: 1rem;
grid-template-columns: repeat (auto-fit, minmax(250px, 1fr));


}
.note.new{
background-color: rgb(185, 194, 194);
}

textarea{
  border: none;
  resize: none;
  background-color: azure;

}

textarea:focus{
  outline: none;
}

.save{
  background-color: rgb(207, 188, 140);
  border: none;
  border-radius: 15px;
  padding: 5px 10px 5px 10px;
}
.save:hover{
background-color: yellow;
cursor: pointer;

}

.note{
background-color: blanchedalmond;
border-radius: 10px;
padding: 1rem;
min-height: 170px;
display: flex;
flex-direction: column;
justify-content: space-between;
white-space: pre-wrap;
}

.note-footer{
display: flex;
align-items: center;
justify-content: space-between;

}

.delete-icon {
cursor: pointer;
}

.search {
  display: flex;
  align-items: center;
  background-color: rgb(233, 233, 233);
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.5em;
   
}

.search input {
border: none;
background-color: rgb(233, 233, 233);

}

.search input:focus{
  outline : none;

}